import React from 'react';

const Chord = (props) => {
	const styles = {
		chordContainer:{
			height:"100%",
			width:"15%",
			borderWidth:2,
		 	borderRadius: 5,
	  		borderColor:"black",
	  		borderStyle: "solid",
	  		marginRight:"1%"
		},
		imageContainer:{
			height:"95%",
			marginTop:"5%",
			width:"100%"
		},
		box:{
			width:"100%",
			height:"100%",
			textAlign:"center",
			paddingTop:"10%",
			fontSize:"4.2rem"
		}
};
	const generateRandomColor = () =>{
		var arr = ["#c5cae9","#b3e5fc","#c8e6c9","#fff9c4","#ffccbc","#f5f5f5","#b0bec5"];
		var randomItem = arr[Math.floor(Math.random()*arr.length)];
		return randomItem;
	}
	styles.box.backgroundColor = generateRandomColor();
	return (
		<div style={styles.chordContainer}>
			<div style ={styles.box}>
			{props.chord.symbol}
			{/* <div style={styles.imageContainer}>
				<img src={props.chord.imgSrc} alt="image" style={styles.image}></img>
			</div> */}
			</div>
		</div>
	)
}

export default Chord;


